import styled from "styled-components"
import React, { useEffect } from "react"
import { useSelector, useDispatch } from "react-redux"
import { changeSearchword, changeCurLocation } from "../actions/index"

const ImgContainer = styled.div`
    position: relative;
    width: 100%;
    height: var(--mobile-page-height);

    @media screen and (min-width: 1081px) {
        height: var(--desktop-page-height);
    }
`

export default function Location(props) {
    const dispatch = useDispatch()
    const { searchWord } = useSelector((state) => state.itemReducer)
    const { kakao } = window

    console.log(searchWord)
    console.log(props)

    useEffect(() => {
        var container = document.getElementById("map")
        var options = {
            center: new kakao.maps.LatLng(37.5642135, 127.0016985),
            level: 3,
        }
        var map = new kakao.maps.Map(container, options) // 
        var zoomControl = new kakao.maps.ZoomControl() // 
        map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT) //  
        //     
        // var marker = new kakao.maps.Marker({
        //     //    
        //     position: map.getCenter(),
        // })
        //   
        // marker.setMap(map)
        // 
        kakao.maps.event.addListener(map, "click", function (mouseEvent) {
            //  ,   
            var latlng = mouseEvent.latLng
            //     
            // marker.setPosition(latlng)
            //      
            var message = "   " + latlng.getLat() + " , "
            message += " " + latlng.getLng() + " "
            console.log(message)
        })

        // HTML5 geolocation    
        if (navigator.geolocation) {
            // GeoLocation    
            navigator.geolocation.getCurrentPosition(function (position) {
                var lat = position.coords.latitude, // 
                    lon = position.coords.longitude // 

                var locPosition = new kakao.maps.LatLng(lat, lon), //    geolocation   
                    message = '<div style="padding:5px;">  </div>' //   

                //   
                displayMarker(locPosition, message)
                console.log(locPosition)

                console.log(lat) //
                console.log(lon) //
                dispatch(changeCurLocation(lat, lon))
            })
        } else {
            // HTML5 GeoLocation         

            var locPosition = new kakao.maps.LatLng(33.450701, 126.570667),
                message = "geolocation  .."

            displayMarker(locPosition, message)
        }

        //     
        function displayMarker(locPosition, message) {
            //  
            var marker = new kakao.maps.Marker({
                map: map,
                position: locPosition,
            })

            var iwContent = message, //   
                iwRemoveable = true

            //  
            var infowindow = new kakao.maps.InfoWindow({
                content: iwContent,
                removable: iwRemoveable,
            })

            //   
            infowindow.open(map, marker)

            //    
            map.setCenter(locPosition)
        }

        // -   
        var geocoder = new kakao.maps.services.Geocoder()

        //   
        geocoder.addressSearch(searchWord, function (result, status) {
            //   
            if (status === kakao.maps.services.Status.OK) {
                var coords = new kakao.maps.LatLng(result[0].y, result[0].x)

                //     
                // var marker = new kakao.maps.Marker({
                //     map: map,
                //     position: coords,
                // })
                console.log(arguments)
                //     
                // var infowindow = new kakao.maps.InfoWindow({
                //     content: `<div style="width:150px;text-align:center;padding:6px 0;">${arguments[0][0].road_address.address_name} </div>`,
                // })
                // infowindow.open(map, marker)

                //      
                dispatch(changeSearchword(searchWord))
                map.setCenter(coords)
            }
        })
    }, [
        kakao.maps.LatLng,
        kakao.maps.Marker,
        kakao.maps.event,
        kakao.maps.Map,
        searchWord,
    ])

    return <ImgContainer id="map"></ImgContainer>
}
